// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueHighlightJS from 'vue-highlightjs'
import {Menu, MenuItem, MenuItemGroup, Row, Col,Icon,Divider } from 'element-ui'

Vue.use(MenuItem).use(MenuItemGroup).use(Menu).use(Row).use(Col).use(Icon).use(Divider).use(VueHighlightJS);
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



