<style scoped>
.headerBox {
  border-bottom: solid 1px #e6e6e6;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 99;
}
.menu {
  /* padding: 0 300px; */
  /* width: 1200px; */
