}
.contantIcon{
  border-top: solid 1px #e6e6e6;
  border-bottom: solid 1px #e6e6e6;
  padding: 15px 0;
  margin: 20px 0;
}
.contantIcon img{
  width: 20px;
}
.el-divider--vertical {
    margin-top: -7px;
}
.readMore{
  float: right;
  color: #1e90ff;
}
.clearfix{
clear: both;
}
</style>


